import React, { Component }  from 'react';
import {useState} from 'react'
import axios from "axios"
const Texttype = ({addText})=> {
    const[mytext, typedText]= useState('')
    const onTurnin=(e)=>{
        e.preventDefault()
        if(!mytext){
            alert("no message entered")
            return
        }
        const newMessage={
            title:mytext
          }
          
          axios.post('http://localhost:3001/create', newMessage)
       addText(mytext)
       typedText('')
    }
    return (
        <div className="Texttype"  onSubmit={onTurnin}>
        <form>
        <input type='text' placeholder={"what's on your mind?"} value={mytext} 
        onChange={(e)=> typedText(e.target.value)}>
        </input>
        <input type="submit"></input>
        </form>
       
        
        </div>
        
    )
}

export default Texttype